const state = {
  nm: '南阳',
  id: '',
  py: 'nanyang'
}
const mutations = {
  CITY_INFO (state, payload) {
    state.nm = payload.nm
    state.id = payload.id
    state.py = payload.py
  }
}

const actions = {
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}
