<template>
  <div>
    <header>
      <div class="head">
        <div class="f">
          <div class="f_1" @click="handleRouter"><span>{{this.$store.state.city.nm}}</span>
             <img data-v-4070467a="" src="data:image/png;base64," width="6px" height="3px">
          </div>
          <div class="f_2">影院</div>
          <div class="f_3"><i class="iconfont icon-sousuo"></i></div>
        </div>
        <div class="s">
          <div class="s_1" @click="handleClick1()"> <span>全程</span>
             <img data-v-4070467a="" src="data:image/png;base64," width="6px" height="3px">
          </div>
          <div class="s_2" @click="handleClick2()">APP订票</div>
          <div class="s_3" @click="handleClick3()">最近去过</div>
        </div>
        <div v-show="isShow1" class="same">第一个</div>
        <div v-if="isShow2" class="same">第二个</div>
        <div v-if="isShow3" class="same">第三个</div>
      </div>
      <div class="cinema-list-d">
        <ul class="cinema-list-u">
          <li class="cinema-list-l" v-for="data in this.cinemaList" :key="data.cineamId">
            <a href="">
              <div class="left">
                <span class="span1">{{data.name}}</span>
                <span class="span2">{{data.address}}</span>
              </div>
              <div class="right">
                <div class="rdiv">
                  <span class="span1"><i>￥</i></span>
                  <span class="span2">54</span>
                  <span class="span3">起</span>
                </div>
                <span class="rspan"> <strong>距离未知</strong></span>
              </div>
            </a>
          </li>
        </ul>
      </div>
    </header>
    <foot></foot>
  </div>
</template>
<script>
import Vue from 'vue'
import axios from 'axios'
import '@/../public/incofont02/iconfont.css'
import foot from '@/components/Footer'
Vue.component('foot', foot)
export default {
  data () {
    return {
      isShow1: false,
      isShow2: false,
      isShow3: false,
      num: '0',
      cinemaList: ''
    }
  },
  mounted () {
    // axios.get('https://m.maizuo.com/gateway?k=8237611').then((res) => {
    //   console.log(res.data)
    // })
    console.log(this.$store.state.city)
    axios({
      url: 'https://m.maizuo.com/gateway?cityId=440300&ticketFlag=1&k=7832857',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16159910672089342675714049","bc":"440300"}',
        'X-Host': 'mall.film-ticket.cinema.list'
      }
    }).then((res) => {
      // console.log(res.data.data)
      this.cinemaList = res.data.data.cinemas
    })
  },

  methods: {
    handleRouter () {
      this.$router.push('/city')
    },
    handleClick1 () {
      if (this.num === 1) {
        this.isShow1 = false
        this.num = 0
        return
      }
      this.isShow1 = true
      this.isShow2 = false
      this.isShow3 = false
      this.num++
    },
    handleClick2 () {
      if (this.num === 1) {
        this.isShow2 = false
        this.num = 0
        return
      }
      this.isShow2 = true
      this.isShow1 = false
      this.isShow3 = false
      this.num++
    },
    handleClick3 () {
      if (this.num === 1) {
        this.isShow3 = false
        this.num = 0
        return
      }
      this.isShow3 = true
      this.isShow1 = false
      this.isShow2 = false
      this.num++
    }
  }
}
</script>
