<template>
  <div>
    <!-- <swiper :key="datalist.name">
      自处不能用ul li标签，看文档是div标签
      <div class="swiper-slide" v-for="data in datalist" :key="data.bannerId">
        <img
          :class="data.bannerId === 390 ? 'tang' : ''"
          :src="data.imgUrl"
          :alt="data.name"
        />
      </div>
    </swiper> -->
       <filmheader :class="isFixed?'fixed':''" ref="myfilm"></filmheader>
      <router-view></router-view>
      <foot></foot>
  </div>
</template>
<script>
import axios from 'axios'
import Vue from 'vue'
import swiper from '@/views/Swiper.vue'
import filmheader from '@/components/FilmHeader'
import Complaying from '@/views/Films/Comingsoon'
import Nowplaying from '@/views/Films/Nowplaying'
import Footer from '@/components/Footer'
import { messageBox } from '@/components/Js'
Vue.component('swiper', swiper)
Vue.component('foot', Footer)
Vue.component('nowplaying', Nowplaying)
Vue.component('comingsoon', Complaying)
Vue.component('filmheader', filmheader)
export default {
  data () {
    return {
      datalist: [],
      nisShow: false,
      cisShow: true,
      isFixed: false
    }
  },
  methods: {
    handleChangeClick (e) {},
    handlescroll () {
      // console.log(this.$refs.myfilm.$el.offsetHeight) // swiper的高度
      // console.log(document.documentElement.scrollTop) // 鼠标滑动的高度
      if (document.documentElement.scrollTop >= 193) {
        this.$refs.myfilm.isShow = true
        this.isFixed = true
        // console.log(this.$refs.myfilm.isShow)
      } else {
        this.isFixed = false
        this.$refs.myfilm.isShow = false
        // console.log(this.$refs.myfilm.isShow)
      }
    }
  },
  mounted () {
    const that = this
    setTimeout(() => {
      axios.get('/json').then(function (res) {
        console.log(res.data)
        const cityList = JSON.parse(window.localStorage.getItem('cityList'))
        const py1 = res.data.city.toLowerCase()
        // console.log(typeof py1)
        let py2 // axios请求定位城市的pinyin
        // const nowCity = that.$store.state.nowCity
        // console.log(nowCity)
        for (let i = 0; i < cityList.length; i++) {
          // console.log('i')
          for (let j = 0; j < cityList[i].list.length; j++) {
            // console.log('j')
            if (py1 === cityList[i].list[j].pinyin) {
              py2 = cityList[i].list[j].nm
              // console.log(py2)
              that.$store.commit('nowcity', { nowCity: py2 })
              break
            }
          }
        }

        const msg = res.data.status
        if (msg === 'success') {
          if (res.data.city.toLowerCase() === that.$store.state.city.py) { // 定位当前城市，地位弹出就不弹出来了.
            console.log('不弹')
            return
          }
          messageBox({
            title: '定位',
            content: that.$store.state.city.nm || that.$store.state.nowCity,
            cancel: '取消',
            ok: '切换定位',
            handleCancel () {
            // console.log('1')
            },
            handleOk () {
            // console.log('2')
              that.$router.push('/city')
              console.log(res.data.city.toLowerCase())
            }
          })
        }
      }).catch(e => console.log(e))
    }, 1000)
    axios({
      url: 'https://m.maizuo.com/gateway?type=2&cityId=440300&k=5733205',
      headers: {
        'X-Client-Info':
          '{"a":"3000","ch":"1002","v":"5.0.4","e":"1615889219745679337029633"}',
        'X-Host': 'mall.cfg.common-banner'
      }
    }).then((res) => {
      this.datalist = res.data.data
    })
    window.onscroll = this.handlescroll
  },
  beforeRouteLeave (to, from, next) {
    next()
    // console.log(from.fullPath)
    window.onscroll = null // 把滚动条事件取消,不然报错
    // console.log('组件级路由钩子')
    // const messageBox = document.getElementsByClassName('messageBox')
    // document.body.removeChild(messageBox)
  },
  beforeDestory () {
  },
  Destoryed () {
  }
}
</script>
<style lang="scss" scoped>
.swiper-slide .tang {
  margin-top: 32px;
}
.fixed{
  position: fixed;
  top: 0;
  left: 0;
  background: white;
  z-index: 100;
  width: 100%;
}
</style>
