<template>
  <div v-if="datalist">
      <div class="head" v-if="head">
      <p>{{this.datalist.name}}</p>
    </div>
    <div class="imghead">
      <img :src="this.datalist.poster" :alt="this.datalist.name" />
    </div>
    <div class="context">
      <div class="contexthead">
        <div class="left">
          <span >{{ this.datalist.name }}</span>
          <span>{{ this.datalist.item.name }}</span>
        </div>
        <div class="right">
          <span v-if="this.datalist.grade">{{ this.datalist.grade }}</span>
          <span v-if="this.datalist.grade">分</span>
        </div>
      </div>
      <p class="p1">{{ this.datalist.category }}</p>
      <p class="p2">2021-03-12上映</p>
      <p class="p3">{{ this.datalist.nation }}|{{ this.datalist.runtime }}</p>
      <p class="p4">{{ this.datalist.synopsis }}</p>
    </div>
    <div class="actors">
      <h3>演职人员</h3>
      <div class="padding-left">
      <nowswiper swiper='4' class="actorsswiper" myclassname='actorsswiper'>
        <div
          class="swiper-slide"
          v-for="data in datalist.actors"
          :key="data.name"
        ><div class="actor">
          <img :src="data.avatarAddress" alt="" />
          </div>
          <p class="actorName">{{data.name}}</p>
          <p class="actorRole">{{data.role}}</p>
        </div>
      </nowswiper>
      </div>
    </div>
    <div class="stills">
      <div class="stills-title-bar">
      <h3>剧照</h3>
      <span class="">
        全部({{this.datalist.photos.length}})
        <i>></i>
      </span>
      </div>
      <div class="padding-left">
      <nowswiper swiper='2.3' class="stillsswiper" myclassname='stillsswiper'>
        <div
          class="swiper-slide"
          v-for="(data, index) in datalist.photos"
          :key="index"
        ><div class="lazy-img">
          <img :src="data" alt="" />
        </div>
        </div>
      </nowswiper>
      </div>
    </div>
    <a href="#" class="footer">
      <div>
        选座购票
      </div>
    </a>
  </div>
</template>
<script>
import axios from 'axios'
import Vue from 'vue'
import nowswiper from '../NowplayingFilm/nowplayingFilm/nowswiper.vue'
Vue.component('nowswiper', nowswiper)
export default {
  components: { nowswiper },
  data () {
    return {
      datalist: '', // 此处定义成空数组浏览器会报错
      actors: 4,
      stills: 'auto',
      head: false
    }
  },
  mounted () {
    // console.log(this.$route.params.id)
    axios({
      url: `https://m.maizuo.com/gateway?filmId=${this.$route.params.id}&k=9194197`,
      headers: {
        'X-Client-Info':
          '{"a":"3000","ch":"1002","v":"5.0.4","e":"16159910672089342675714049"}',
        'X-Host': 'mall.film-ticket.film.info'
      }
    }).then((res) => {
      this.datalist = res.data.data.film
      // console.log(this.datalist)
    })
    window.onscroll = this.handlescroll
  },
  methods: {
    handlescroll () {
      if (document.documentElement.scrollTop >= 50) {
        this.head = true
        // console.log(document.documentElement.scrollTop)
      } else {
        this.head = false
      }
    }
  },
  beforeDestroy () {
    // window.onscroll = null
  }
}
</script>
