<template>
    <div class="recommend-city">
    <div class="gprs-city">
        <div class="city-index-title">GPS定位你所在城市</div>
        <ul class="city-index-detail cleanfix">
            <li class="city-item-detail city-item-detail-gprs">
                <div class="city-item-text" @click="gpsToCity('nanyang')">{{ this.$store.state.nowCity }}</div>
            </li>
        </ul>
    </div>
    <div class="hot-city">
        <div class="city-index-title">热门城市</div>
        <ul class="city-index-detail cleanfix">
            <li class="city-item-detail" v-for="hot in hotList" :key="hot.cityId">
                <div class="city-item-text" @click="handleToCity(hot.name, hot.cityId, hot.pinyin)">{{hot.name}}</div>
            </li>
        </ul>
    </div>
    </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      hotList: '',
      cityList: ''
    }
  },
  mounted () {
    axios({
      url: 'https://m.maizuo.com/gateway?k=6907530',
      headers: {
        'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"1615889219745679337029633","bc":"210300"}',
        'X-Host': 'mall.film-ticket.city.list',
        'X-Requested-With': 'XMLHttpRequest'
      }
    }).then((res) => {
      // console.log(res.data.data)
      const cities = res.data.data.cities
      var obj = this.formatCityList(cities)
      this.hotList = obj.hotList
      this.cityList = obj.cityList
      // console.log(this.cityList)
      // console.log(this.hotList)
    })
  },
  methods: {
    formatCityList (cities) {
      const cityList = []
      const hotList = []
      for (let i = 0; i < cities.length; i++) {
        var firstLetter = cities[i].pinyin.substring(0, 1).toUpperCase()
        if (toCom(firstLetter)) { // 添加到index
          cityList.push({ index: firstLetter, list: [{ nm: cities[i].name, id: cities[i].cityId }] })
        } else { // 累加到已有索引
          for (let j = 0; j < cityList.length; j++) {
            if (firstLetter === cityList[j].index) {
              cityList[j].list.push({ nm: cities[i].name, id: cities[i].cityId })
            }
          }
        }
      }
      cityList.sort(function (a, b) {
        if (a.index < b.index) {
          return -1
        } else if (a.index > b.index) {
          return 1
        } else {
          return 0
        }
      })
      //   console.log(cityList)
      function toCom (firstLetter) {
        for (let i = 0; i < cityList.length; i++) {
          if (cityList[i].index === firstLetter) {
            return false
          }
        }
        return true
      }

      // 热门城市
      for (let i = 0; i < cities.length; i++) {
        if (cities[i].isHot === 1) {
          hotList.push(cities[i])
        }
      }
      return { cityList, hotList }
    },
    handleToCity (nm, id, py) {
      this.$store.commit('city/CITY_INFO', { nm, id, py }) // 修改state中的状态
      // this.$store.commit('nowcity', { nowCity: nm })
      // console.log(this.$store.state.city.py)
      this.$router.push('/films/nowplaying')
    },
    gpsToCity (nowCity) {
      this.$store.commit('nowcity', { nowCity })
      this.$store.commit('city/CITY_INFO', { nm: '南阳', py: nowCity })
      this.$router.push('/films/nowplaying')
      console.log(this.$store.state.city.py)
    }
  }
}
</script>
<style lang="scss" scoped>
    .recommend-city{
        // position: fixed;
        position: absolute;
        box-sizing: border-box;
        list-style: none;
        background-color: #fff;
        padding-left: 15px;
        padding-top: 15px;
        top: 93px;
        width: 100%;
        z-index: 100;
    .gprs-city{
        box-sizing: border-box;
        .gprs-city ul,li{
            list-style: none;
        }
        .city-index-title{
            font-size: 13px;
            color: #797d82;
            margin-bottom: 10px;
        }
        .city-index-detail{
            display: flex;
            flex-wrap: wrap;
            align-content: center;
            // justify-content: flex-start;
            padding: 0;
            .city-item-detail-gprs{
                width: calc(33.33333vw - 11px);
                text-align: center;
                padding-bottom: 15px;
                box-sizing: border-box;
                float: left;
            .city-item-text{
                height: 30px;
                background-color: #f4f4f4;
                line-height: 30px;
                border-radius: 3px;
                box-sizing: border-box;
                margin: 0 7.5px;
                font-size: 14px;
                color: #191a1b;
                text-align: center;
            }
            }
        }
    }
      .hot-city{
          box-sizing: border-box;
           ul,li{
            list-style: none;
            padding-left: 0;
        }
        .city-index-title{
            font-size: 13px;
           color: #797d82;
           margin-bottom: 10px;
        }
        .city-index-detail{
            display: flex;
            justify-content: flex-start;
            flex-wrap: wrap;
            margin: 0;
            .city-item-detail{
                width: calc(33.33333vw - 11px);
                text-align: center;
                padding-bottom: 15px;
                box-sizing: border-box;
                // float: left;
                .city-item-text{
                    height: 30px;
                    background-color: #f4f4f4;
                    line-height: 30px;
                    border-radius: 3px;
                    box-sizing: border-box;
                    margin: 0 7.5px;
                    font-size: 14px;
                    color: #191a1b;
                }
            }
        }
      }
    }
</style>
