<script>
import bus from '@/views/bus/bus.js'
export default {
  data () {
    return {
      nisShow: true,
      cisShow: true,
      isFixed: false
    }
  },
  methods: {
  },
  mounted () {
    bus.$on('nowplaying', (data) => { // data 就是发送过来的数据
      this.nisShow = data
      console.log('n被通知了' + data)
    })
    bus.$on('comingsoon', (data) => {
      this.cisShow = data
      console.log("'c被通知了'" + data)
    })
  },
  beforeDestroy () {
    // console.log('beforeDestroy')
    window.onscroll = null// 离开此组件，就销毁window.onscroll事件
  }
}
</script>
