import Vue from 'vue'
import VueRouter from 'vue-router'
import Films from '@/views/Films'
import Nowplaying from '@/views/Films/Nowplaying'
import Comingsoon from '@/views/Films/Comingsoon'
import nowplayingfilm from '@/views/Film/NowplayingFilm'
import comingsoonfilm from '@/views/Film/ComingsoonFilm'
import Cinemas from '@/views/Cinema'
import Consult from '@/views/Consult'
import CityList from '@/views/CityList'
import Login from '@/views/Login'
Vue.use(VueRouter) // 使用注册路由的插件
const router = new VueRouter({
  mode: 'hash',
  base: '/',
  routes: [ // router自创的,实际用routes ，坑。。暂不知道router
    {
      path: '/films',
      component: Films,
      children: [
        {
          path: 'nowplaying',
          component: Nowplaying
        },
        {
          path: 'comingsoon',
          component: Comingsoon
        }
      ]
      // beforeEnter: (to, from, next) => {
      //   next()
      //   console.log('单独的路由守卫')
      // }
    },
    {
      path: '/film/:id',
      name: 'nowplayingfilm',
      component: nowplayingfilm
    },
    {
      path: '/film/:id',
      name: 'comingsoonfilm',
      component: comingsoonfilm
    },
    {
      path: '/cinemas',
      component: Cinemas
    },
    {
      path: '/consult',
      component: Consult
    },
    {
      path: '/city',
      component: CityList
    },
    {
      path: '/login',
      component: Login
    },
    {
      path: '*',
      redirect: '/films/nowplaying'
    }
  ]
})

// 全局路由
// router.beforeEach((to, from, next) => {
//   // console.log(to.path)
//   window.onscroll = null
//   next()
//   console.log('全局路由钩子beforeEach')
// })
router.afterEach((to, from) => {
  document.documentElement.scrollTop = 0
  // console.log('全局路由钩子afterEach')
})
export default router
