<template>
  <div>
    <ul class="n-u">
      <li class="n-l" v-for="data in datalist" :key="data.filmId" @click="handleChangePage(data.filmId)">
        <img :src="data.poster" :alt="data.name"  class="n-i"/>
        <div class="n-c">
          <div class="film-name">
          <span class="name">{{ data.name}}</span><span class="item">{{data.item.name}}</span></div>
          <span v-if="data.grade">观众评分<span class="score"> {{ data.grade }}</span></span>
          <span v-else class="hide">观众评分</span>
          <span v-if="data.director">主演: {{ data.director }}</span>
          <span v-else>暂无主演</span>
          <span>{{ data.nation}} | {{data.runtime}}</span>
        </div>
        <div class="byTicket">购票</div>
      </li>
    </ul>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      datalist: []
    }
  },
  mounted () {
    axios({
      url:
        'https://m.maizuo.com/gateway?cityId=110100&pageNum=1&pageSize=10&type=1&k=9116520',
      // 'https://m.maizuo.com/gateway?cityId=411300&pageNum=1&pageSize=10&type=1&k=9965096',
      headers: {
        'X-Client-Info':
          '{"a":"3000","ch":"1002","v":"5.0.4","e":"16159910672089342675714049","bc":"110100"}',
        'X-Host': 'mall.film-ticket.film.list'
      }
    }).then((res) => {
      //   console.log(res.data.data.films[0])
      this.datalist = res.data.data.films
      // console.log(res.data.data.films)
    })
    window.onscroll = function () {
      // console.log(document.body.scrollHeight)
      // console.log(document.scrollTop)
      // console.log()
    }
  },
  methods: {
    handleChangePage (id) {
      // console.log(id)
      // 编程式导航-路径跳转
      this.$router.push(`/film/${id}`)

      // 编程式导航-名字跳转-params
      // this.$router.push({
      //   name: 'nowplayingfilm',
      //   params: { id: id }
      // })

      // 编程式导航-名字跳转-query
      // this.$router.push({
      //   name: 'nowplayingfilm',
      //   query: { id: id }
      // })
    }
  }
}
</script>
<style lang="scss" scoped>
  div {
    ul,li{list-style: none;margin: 0; padding: 0;}
  width: 100%;
  .n-u {
    list-style: none;
    margin: 0px;
    margin-left: 15px;
    padding: 0px;
    .n-l {
      width: 100%;
      padding-top: 15px;
      padding-bottom: 15px;
      padding-right: 15px;
      box-sizing: border-box;
      display: flex;
      position: relative;
      img {
        width: 66px;
        height: 94px;
      }
      .n-c {
        padding-right: 10px;
        padding-left: 10px;
        width: calc(100% - 116px);
        box-sizing: border-box;
        font-size: 13px;
        color: #797d82;
        position: relative;
        span{
          color: #191a1b;
          margin-top: 4px;
          display: block;
          // font-size: 13px;
          color: #797d82;
          white-space: nowrap;
          font-family: Microsoft YaHei,Tahoma,Helvetica,Arial,sans-serif;
          font: 13px Tahoma,Helvetica,Arial,sans-serif;
        }
        .hide{
          opacity: 0;
        }
        .score{
          color: #ffb232;
          font-size: 14px;
          display: inline;
          font-family: Microsoft YaHei,Tahoma,Helvetica,Arial,sans-serif;
        }
        .film-name{
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          width: 100%;
          color: #333;
          // line-height: 100%;
        .name{
          max-width: calc(100% - 25px);
          color: #191a1b;
          font-size: 16px;
          height: 22px;
          line-height: 22px;
          margin-right: 5px;
          margin-top: 0px;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          display: inline-block;
        }
        .item{
          display: inline-block;
          font-size: 9px;
          color: #fff;
          background-color: #d2d6dc;
          height: 14px;
          padding: 0 2px;
          border-radius: 2px;
          margin: 0;
          display: inline-block;
          position: relative;
          top: -6px;
        }
        }
      }
      .byTicket{
        line-height: 25px;
        height: 25px;
        width: 50px;
        color: #ff5f16;
        font-size: 13px;
        border-radius: 2px;
        text-align: center;
        position: relative;
        border: 1px solid #ff5f16;
        top: 25px;
      }
    }
  }
}
</style>
