import Vue from 'vue'
import App from './App.vue'
import router from './router' // import router from './router.js'  .js可以省略
import store from './store'

Vue.config.productionTip = false

new Vue({
  router, // router: router  key值和后面引用的变量名一样可以简写---> router,对象简写
  store,
  render: h => h(App)
}).$mount('#app')
