<template>
  <div class="zuida">
    <cityhead></cityhead>
    <recommendcity></recommendcity>
    <ul class="mint-indexlist-content" ref="city_sort">
      <li
        class="mint-indexsection city-index-section"
        v-for="data in this.cityList"
        :key="data.index"
      >
        <p class="mint-indexsection-index">{{ data.index }}</p>
        <ul class="ul">
          <li class="city-item-detail-white" v-for="x in data.list" :key="x.id">
            <div class="city-item-text in-section" @click="handleToCity(x.nm, x.id, x.pinyin)">{{ x.nm }}</div>
          </li>
        </ul>
      </li>
    </ul>
    <ul class="letter-u">
      <li
        class="letter-l"
        v-for="(value, index) in this.cityList"
        :key="index"
        @click="handleClick(index)"
      >
        {{ value.index }}
      </li>
    </ul>
    <div class="popup" v-show="isShow">
      {{ abc }}
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import Vue from 'vue'
import cityListHead from '@/components/CityList/CityListHead'
import recommendCity from '@/components/CityList/RecommendCity'
Vue.component('cityhead', cityListHead)
Vue.component('recommendcity', recommendCity)
export default {
  data () {
    return {
      hotList: '',
      cityList: '',
      abc: '',
      isShow: false
    }
  },
  mounted () {
    var cityList = window.localStorage.getItem('cityList')
    var hotList = window.localStorage.getItem('hotList')

    if (cityList && hotList) {
      // console.log('执行了windwo.localStorage')
      this.cityList = JSON.parse(cityList)
      this.hotList = JSON.parse(hotList)
    } else {
    // console.log('执行axios')
      axios({
        url: 'https://m.maizuo.com/gateway?k=6907530',
        headers: {
          'X-Client-Info':
          '{"a":"3000","ch":"1002","v":"5.0.4","e":"1615889219745679337029633","bc":"210300"}',
          'X-Host': 'mall.film-ticket.city.list',
          'X-Requested-With': 'XMLHttpRequest'
        }
      }).then((res) => {
      // console.log(res.data)
        const cities = res.data.data.cities
        // [{ index: 'A', list:[{ nm: '啊城', id: 123}] }]
        var obj = this.formatCityList(cities)
        this.hotList = obj.hotList
        this.cityList = obj.cityList
        // console.log(this.cityList)
        window.localStorage.setItem('cityList', JSON.stringify(obj.cityList))
        window.localStorage.setItem('hotList', JSON.stringify(obj.hotList))
      })
    }
  },
  methods: {
    formatCityList (cities) {
      const cityList = []
      const hotList = []
      for (let i = 0; i < cities.length; i++) {
        var firstLetter = cities[i].pinyin.substring(0, 1).toUpperCase()
        if (toCom(firstLetter)) {
          // 添加到index
          cityList.push({
            index: firstLetter,
            list: [{ nm: cities[i].name, id: cities[i].cityId, pinyin: cities[i].pinyin }]
          })
        } else {
          // 累加到已有索引
          for (let j = 0; j < cityList.length; j++) {
            if (firstLetter === cityList[j].index) {
              cityList[j].list.push({
                nm: cities[i].name,
                id: cities[i].cityId,
                pinyin: cities[i].pinyin
              })
            }
          }
        }
      }
      cityList.sort(function (a, b) {
        if (a.index < b.index) {
          return -1
        } else if (a.index > b.index) {
          return 1
        } else {
          return 0
        }
      })
      //   console.log(cityList)
      function toCom (firstLetter) {
        for (let i = 0; i < cityList.length; i++) {
          if (cityList[i].index === firstLetter) {
            return false
          }
        }
        return true
      }

      // 热门城市
      for (let i = 0; i < cities.length; i++) {
        if (cities[i].isHot === 1) {
          hotList.push(cities[i])
        }
      }
      //   console.log('热门城市-->' + hotList[0].name)
      return { cityList, hotList }
    },
    handleClick (index) {
      const p = this.$refs.city_sort.getElementsByTagName('p')
      document.documentElement.scrollTop = p[index].parentNode.offsetTop - 140
      const letter = p[index].innerHTML
      this.abc = letter

      // 设置弹窗
      this.isShowT = true
      setTimeout(() => { this.isShowT = false }, 800) // 这里不使用箭头函数 this指向的是window
      // const that = this
      // setTimeout(function () {
      //   that.isShowT = false
      //   console.log(this)
      // }, 1000)
    },
    handleToCity (nm, id, py) { // 修改state的状态,跳转到
      this.$store.commit('city/CITY_INFO', { nm, id, py })
      // console.log(this.$store.state.city.py)
      this.$router.push('/films/nowplaying')
    }
  }
}
</script>
<style lang="scss" scoped>
.zuida {
  ul,
  li {
    list-style: none;
  }
  .mint-indexlist-content {
    margin: 0px 17px 0px 0px;
    height: 100vh !important;
    position: relative;
    // overflow-y: scroll;
    padding: 0;
    top: 0;
    .mint-indexsection {
      width: 100%;
      position: relative;
      margin: 0;
      padding: 0;
      top: 318.6px;
      .mint-indexsection-index {
        height: 30px;
        line-height: 30px;
        background-color: #f4f4f4;
        color: #797d82;
        padding: 0 0 0 15px;
        box-sizing: border-box;
        margin: 0;
      }
      .ul {
        position: relative;
        padding: 0 0 0 20px;
        margin: 0;
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        justify-content: flex-start;
        .city-item-detail-white {
          color: #191a1b;
          box-sizing: border-box;
          height: 48px;
          width: 100%;
          padding: 0;
          margin: 0;
          list-style: none;
          .in-section {
            background-color: #fff;
            height: 100%;
            line-height: 48px;
            font-size: 14px;
            box-sizing: border-box;
            border-bottom: 1px solid #ededed;
          }
        }
      }
    }
  }
  .letter-u {
    position: fixed;
    right: 0;
    top: 27%;
    // display: flex;
    // flex-direction: column;
    margin: 0;
    padding: 0;
    font-size: 12px;
    color: #191a1b;
    z-index: 101;
    .letter-l {
      text-align: center;
      margin-bottom: 1px;
    }
  }
  .popup {
    width: 50px;
    height: 50px;
    border-radius: 5px;
    color: #ffffff;
    background-color: #797d82;
    text-align: center;
    line-height: 50px;
    position: fixed;
    top: calc(50% - 25px);
    left: calc(50% - 25px);
  }
}
</style>
