import Vue from 'vue'
import Vuex from 'vuex'
import city from './city'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    nowCity: '南阳'
  },
  mutations: {
    nowcity (state, payload) {
      state.nowCity = payload.nowCity
      // console.log('执行了mutataions')
    }
  },
  actions: {
  },
  modules: {
    city
  }
})
